@import url('https://fonts.googleapis.com/css?family=K2Dr&subset=latin');


body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: 'K2D';
  }
  
.App {
    text-align: center;
    /* hide scrollbar for IE, Edge and Firefox */
    -ms-overflow-style: none;
    scrollbar-width: none;
    background-color: white;
}
 
*{
    scroll-behavior: smooth;
    margin: 0;
}

h1 {
    font-size: 50px;
}
  
/* hide scrollbar for chrome, safari and opera */
::-webkit-scrollbar {
    display: none;
}

:root {
    --landing-section-positioning: fixed;
    --filler-height-1: 0vh;
    --filler-height-2: 200vh;
    --welcome-opacity: 1;
    --letter-rotation: 45deg;
}

.LandingSection {
    position: var(--landing-section-positioning);
    width: 100vw;
    height: 100vh;
    align-items: center;
    display: flex;
}

.LandingSectionFiller1 {
    position: relative;
    height: var(--filler-height-1);
}

.LandingSectionFiller2 {
    position: relative;
    height: var(--filler-height-2);
}

.NameContainer {
    width: calc(100vw * 13/15);
    margin-left: calc(100vw / 15);
    margin-right: calc(100vw / 15);
    display: flex;
    flex-direction: row;
}

.Letter {
    z-index: 100;
    width: calc(100% / 6);
    transition: all 0.5s;
    scale: 1.3
}

.Letter:hover {
    scale: 1.8;
    rotate: var(--letter-rotation);
}

.WordContainer {
    display: flex;
    width: 100%;
}

.WordSpacer {
    width: calc(100vw * 2 / 15);
    height: 100px;
}

.BackgroundAnimation {
    width: 100vw;
    position: absolute;
}

.ScrollIndicator {
    width: 100px; 
    height: 100px;
    align-items: bottom; 
    margin-left: calc(50vw - 50px);
    justify-content: center;
    position: absolute;
    margin-top: calc(100vh - 140px);
    filter: grayscale(0.2);
    filter: hue-rotate(34deg);
    opacity: 0.7;
}

.WelcomeText {
    line-height: 100vh; 
    text-align: center;
    width: 100vw;
    position: absolute;
    z-index: 1000;
    opacity: var(--welcome-opacity);
}
button {
    all: unset;
    cursor: pointer;
    width: 100%;
    transition: all 0.1s;
}
.burgerMenuButton {
    margin-top: 6px;
    margin-left: 6px;
    height: 28px;
    width: 28px;
    background-color: white;
    z-index: 1000;
}

.MenuContainer {
    background-color: white;
    border: none;
    padding: 10px;
    border-radius: 30px;
    margin-top: 20px;
    box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, .15);
    margin-left: 20px;
    position: fixed;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    width: 40px;
    height: 40px;
    float: left;
    overflow: hidden;
    transition: all 0.2s ease-in-out;
}
  
.MenuContainer.expanded {
    width: 135px;
    height: 250px;
    border-radius: 10px;
}

.page_references {
    float: left;
    text-align: left;
    margin-left: 10px;
    color: black;
    text-decoration: none;
}
  
.LinkContainer {
    width: 100%;
    height:100%;
    justify-content: space-evenly;
    display: flex;
    flex-direction: column;
    float: left;
}

.PageProgressIndicator {
    height: 5px;
    background-color: #2980b9;
    width: 0;
    position: fixed;
    bottom: 0;
    z-index: 1000000000;
    transition: all 0.01s;
}

h2:hover {
    text-shadow: 3px 3px 1px #3498db;
    transform: translate(-3px,-3px);
}
h2 {
    transition: all 0.2s;
}
.profilePicture:hover {
    transform: scale(1.02);
    border-radius: 5%;
}
.profilePicture {
    transition: all 0.2s;
}
.CertificationCard:hover > a {
    scale: 1.05;
    opacity: 1;
}
.ContactSection:hover > h2 {
    transform: none;
}
@media only screen and (max-width: 1000px) {
    .NameContainer {
        flex-direction: column;
    }

    .LandingSectionFiller1 {
        display: none
    }

    .LandingSectionFiller2 {
        display: none
    }

    .LandingSection {
        position: relative;
    }

    .WelcomeText {
        display: none;
    }
    .MenuContainer {
        display: none;
    }
}